import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Typography, IconButton } from '@material-ui/core';
import ClearIcon from '@material-ui/icons/Clear';
import AddIcon from '@material-ui/icons/Add';

const useStyles = makeStyles(theme => ({
    scaled_font: {
        [theme.breakpoints.down("sm")]: {
            fontSize: "8.5pt",
        },
        [theme.breakpoints.up("sm")]: {
            fontSize: "8.7pt",
        },
        [theme.breakpoints.up("md")]: {
            fontSize: "12pt",
        },
        [theme.breakpoints.up("lg")]: {
            fontSize: "13pt",
        },
    },
    label: {
        color: '#111',
        fontFamily: "Arial",
        paddingTop: "10px",
        [theme.breakpoints.down("sm")]: {
            fontSize: "10pt",
            paddingLeft: "15px",
            paddingRight: "35px"
        },
        [theme.breakpoints.up("sm")]: {
            fontSize: "11pt",
            paddingLeft: "25px",
            paddingRight: "35px"
        },
        [theme.breakpoints.up("md")]: {
            fontSize: "12pt",
            paddingLeft: "25px",
            paddingRight: "35px"
        },
        [theme.breakpoints.up("lg")]: {
            fontSize: "13pt",
            paddingLeft: "30px",
            paddingRight: "40px"
        },
        textAlign: "left",
    },
    hr: {
        border: 0,
        height: "1px",
        background: "#777"
    },
    error_box: {
        padding: "2px",
        fontFamily: "Arial",
        border: "1px solid grey"
    },
    error_title: {
        fontSize: "12pt",
        padding: 0,
        margin: 0
    },
    help_icon: {
        width: "15px",
        height: "15px"
    },
    tooltip_text: {

    },
    btn: {
        color: "green"
    }
}));

function BnbHelpText() {
    const classes = useStyles();
    return <Typography className={classes.tooltip_text}>
        PancakeSwap uses constant product formula for price calculation which is basically ratio between sizes of two liquidity pools with main liquidity pool in BNB
        so it might be useful to consider BNB price as real indicator of token performance since token USD price is proportional to BNB price.
    </Typography>;
}

function TableDisplay(props) {
    const { label, value } = props;
    return <p style={{ textAlign: "left" }}>{label}<span style={{ float: "right" }}>{value}</span></p>;
}

function WalletDisplay(props) {
    const classes = useStyles();
    const { wallets, deleteWallet } = props;
    if (!wallets || wallets.length === 0) {
        return null;
    }
    const rows = wallets.map((w, i) => <Typography key={i} className={classes.scaled_font}>{w}
        <IconButton size="small" onClick={() => deleteWallet(i)} style={{ color: '#a50156' }}><ClearIcon /></IconButton>
    </Typography>);
    return (
        <Box style={{ textAlign: "center" }}>
            {rows}
        </Box>
    )
}

